import {
  Col,
  Form,
  Image,
  Input,
  InputNumber,
  message,
  Modal,
  Row,
  Select,
  Upload,
  Button
} from 'antd';
import {
  UploadOutlined,
} from '@ant-design/icons';
import adminApi from '../../../../../apis/adminApi';
import constants from '../../../../../constants/index';
import PropTypes from 'prop-types';
import React, { useEffect, useState, useRef } from 'react';
import Compressor from 'compressorjs';
import imgNotFound from '../../../../../assets/icon/img_notfound.png';
import './index.scss'
function EditProductModal(props) {
  const { visible, onClose, product } = props;
  const { bookId, code, title, brand, discount, price, instock, category, QRcodeImg, publicOfYear, width, height, publisher, author, img } = product ? product : {};
  const [form] = Form.useForm()
  const initValues = { bookId, code, title, brand, discount, price, instock, category, publicOfYear, width, height, publisher, author, img };
  const [isUpdating, setIsUpdating] = useState(false);

  const [avtFileList, setAvtFileList] = useState([]);
  // avt đã nén
  const [avatar, setAvatar] = useState(null);

  // danh sách hình ảnh sp đã nén
  const fileCompressedList = useRef([]);
  // event: Sửa chữa sản phẩm
  const onCompressFile = async (file, type = 0) => {
    new Compressor(file, {
      quality: constants.COMPRESSION_RADIO,
      convertSize: constants.COMPRESSION_RADIO_PNG,
      success(fileCompressed) {
        const reader = new FileReader();
        reader.readAsDataURL(fileCompressed);
        reader.onloadend = async () => {
          if (type === 0) setAvatar(reader.result);
          else if (fileCompressedList.current.length < 10)
            fileCompressedList.current.push({
              data: reader.result,
              uid: file.uid,
            });
        };
      },
      error(err) {
        message.error('Lỗi: ', err);
      },
    });
  };

  const onEdit = async (value) => {
    try {
      console.log(value);
      setIsUpdating(true);
      value.img = avatar;
      const response = await adminApi.updateProduct(value);
      if (response && response.status === 200) {
        message.success('Cập nhật thành công');
        closeModal(value);
      }
    } catch (error) {
      message.error('Cập nhật thất bại');
    }
    setIsUpdating(false);
  };
  const closeModal = (value = null) => {
    setAvatar(null);
    setAvtFileList([])
    onClose(value);

  }
  useEffect(() => {
    form.setFieldsValue(initValues)
  }, [form, initValues])

  return (
    <Modal
      className="edit-product-modal"
      destroyOnClose={false}
      maskClosable={false}
      visible={visible}
      okText="Update"
      cancelText="Cancell"
      onCancel={() => closeModal(null)}
      okButtonProps={{ form: 'editForm', htmlType: 'submit' }}
      title="Update product information"
      confirmLoading={isUpdating}
      width={1000}
      forceRender
      // forceRender dùng để render trước các children của modal những display = none
      // nếu trong modal có form dùng Form.useForm , thì thêm forceRender để tránh warning
      centered>
      <Form
        initialValues={initValues}
        name="editForm"
        onFinish={(value) => onEdit(value)}
        form={form}
        labelCol={{
          span: 5, md: 6, xl: 6
        }}
      >
        <Row gutter={[16, 16]}>
          {/* Id */}
          <Col span={12}>
            <Form.Item
              name="bookId"
              label="ID"
              rules={[{ required: true, message: 'Required' }]}
            >
              <Input disabled size="large" placeholder="ID" />
            </Form.Item>
          </Col>
          {/* Mã sản phẩm */}
          <Col span={12}>
            <Form.Item
              name="category"
              label="Category"
              rules={[{ required: true, message: 'Required' }]}>
              <Select size="large" placeholder="Category *">
                {constants.CATEGORIES.map((item, index) => (
                  <Select.Option value={item.value} key={index}>
                    {item.title}
                  </Select.Option>
                ))}
              </Select>
            </Form.Item>
          </Col>

          {/* Tên sản phẩm */}
          <Col span={12}>
            <Form.Item
              name="title"
              label="Title"
              rules={[
                { required: true, message: 'required', whitespace: true },
              ]}>
              <Input size="large" placeholder="title *" />
            </Form.Item>
          </Col>

          {/* Giá sản phẩm */}
          <Col span={12}>
            <Form.Item
              name="price"
              label="Price"
              rules={[{ required: true, message: 'Required' }]}>
              <InputNumber
                min={0}
                max={9000000000}
                step={1}
                className="w-100"
                size="large"
                placeholder="Price *"
                addonBefore="$"
              />
            </Form.Item>
          </Col>

          {/* Author */}
          <Col span={12}>
            <Form.Item
              name="author"
              label="Author"
              rules={[
                { required: true, message: 'Required', whitespace: true },
              ]}>
              <Input size="large" placeholder="Author *" />
            </Form.Item>
          </Col>

          {/* Thương hiệu */}
          <Col span={12}>
            <Form.Item
              name="instock"
              label="In Stock"
              rules={[{ required: true, message: 'Required' }]}>
              <InputNumber
                style={{ width: '100%' }}
                step={1}
                size="large"
                min={0}
                max={100000}
                placeholder="in Stock *"
              />
            </Form.Item>
          </Col>

          {/* Tồn kho */}
          <Col span={12}>
            <Form.Item
              name="publisher"
              label="Publisher"
              rules={[
                { whitespace: true, message: "Invalid" },
              ]}>
              <Input size="large" placeholder="Publisher *" />
            </Form.Item>
          </Col>
          {/* Mức giảm giá */}
          <Col span={12}>
            <Form.Item
              name="discount"
              label="Discount"
              rules={[{ required: true, message: 'Required' }]}>
              <InputNumber
                style={{ width: '100%' }}
                step={1}
                size="large"
                min={0}
                max={99}
                placeholder="Discount *"
                addonAfter="%"
              />
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item
              name="publicOfYear"
              label="Public Of Year"
              rules={[{ required: true, message: 'Bắt buộc' }]}>
              <InputNumber
                style={{ width: '100%' }}
                step={1}
                size="large"
                min={1950}
                max={new Date().getFullYear()}
                placeholder="Public Of Year *"
              />
            </Form.Item>
          </Col>
          <Col span={12}>
            {/* <Row> */}
            <Form.Item label="Size">
              <Row gutter={8}>
                <Col span={12}>
                  <Form.Item
                    name="width"
                    label="Width"
                    rules={[{ required: true, message: 'Bắt buộc' }]}>
                    <InputNumber
                      style={{ width: '100%' }}
                      step={1}
                      size="large"
                      min={1}
                      max={100}
                      placeholder="width *"
                      addonAfter="cm"
                    />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    name="height"
                    label="Height"
                    rules={[{ required: true, message: 'Required' }]}>
                    <InputNumber
                      style={{ width: '100%' }}
                      step={1}
                      size="large"
                      min={1}
                      max={100}
                      placeholder="height *"
                      addonAfter="cm"
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Form.Item>
            {/* </Row> */}
          </Col>
          <Col span={12}>
            <Image
              width={300}
              src={avatar || img}
              fallback={imgNotFound}
            />
            <div className="text-center">
              <Upload
                listType="picture"
                fileList={avtFileList}
                accept="image/png, image/jpeg"
                // showUploadList={true}
                onChange={({ fileList, file }) => {
                  console.log("AddProduct 295: ", fileList, file)
                  if (avtFileList.length < 1) {
                    setAvtFileList(fileList)
                    setAvatar(URL.createObjectURL(file));
                  }
                }}
                onRemove={() => {
                  setAvatar(null);
                  setAvtFileList([]);
                }}
                beforeUpload={(file) => {
                  onCompressFile(file, 0);
                  return false;
                }}
              >
                <Button
                  disabled={avatar ? true : false}
                  className="w-100 h-100 m-t-10"
                  icon={<UploadOutlined />}
                >
                  Upload Avatar
                </Button>
              </Upload>
            </div>
          </Col>
          <Col span={12}>
            <h1>QR code</h1>
            <Image
              // width={200}
              // height={200}
              src={QRcodeImg}
              fallback="data:image/jpeg;base64,"
            />
          </Col>
        </Row>

      </Form>
    </Modal>
  );
}

EditProductModal.propTypes = {
  onClose: PropTypes.func,
  product: PropTypes.object,
  visible: PropTypes.bool,
};

export default EditProductModal;
