import {
  DeleteOutlined,
  EditOutlined,
  EyeOutlined,
  WarningOutlined,
} from '@ant-design/icons';
import { message, Pagination, Spin, Table, Tooltip } from 'antd';
import Modal from 'antd/lib/modal/Modal';
import adminApi from '../../../../apis/adminApi';
import productApi from '../../../../apis/productApi';
import helpers from '../../../../helpers';
import React, { useEffect, useState } from 'react';
import EditProductModal from './EditProductModal';
import AdminSearch from '../../../../components/AdminSearch';

function generateFilterType() {
  let result = [];
  for (let i = 1; i < 9; ++i) {
    result.push({ value: i, text: helpers.convertProductType(i) });
  }
  return result;
}

function SeeProduct() {
  const [editModal, setEditModal] = useState({ visible: false, product: null });
  const [modalDel, setModalDel] = useState({ visible: false, _id: '' });
  const [isLoading, setIsLoading] = useState(false);
  const [list, setList] = useState([]);
  const [totalProduct, setTotalProduct] = useState(0)
  const [page, setPage] = useState(1)
  // const [loading,setLoading] =useState(true)
  // event: xoá sản phẩm
  const onDelete = async (_id) => {
    try {
      const response = await adminApi.removeProduct(_id);
      if (response && response.status === 200) {
        message.success('Xoá thành công.');
        const newList = list.filter((item) => item._id !== _id);
        setList(newList);
        // setTotal(total - 1);
        //dòng này xem xét lại
      }
    } catch (error) {
      message.error('Xoá thất bại, thử lại !');
    }
  };

  // event: cập nhật sản phẩm
  const onCloseEditModal = (newProduct) => {
    if (newProduct) {
      // Khi chỉnh có cập nhật
      const newList = list.map((item) =>
        item.bookId !== newProduct.bookId ? item : { ...item, ...newProduct },
      );
      setList(newList);
    }
    setEditModal({ visible: false });
  };

  // event: Lấy toàn bộ danh sách sản phẩm
  useEffect(() => {
    let isSubscribe = true;
    setIsLoading(true);
    async function getProductList() {
      try {
        const response = await productApi.getAllProducts(page, 10);
        if (response.data && isSubscribe) {
          const { count, rows } = response.data;
          const list = rows.map((item, index) => {
            return { ...item, key: index };
          });
          setTotalProduct(count)
          setList(list);
          setIsLoading(false);
        }
      } catch (error) {
        console.log("SeeProduct 71: ", error);
        if (isSubscribe) setIsLoading(false);
        message.error('Lấy danh sách sản phẩm thất bại.');
      }
    }
    getProductList();

    return () => {
      isSubscribe = false;
    };
  }, [page]);

  // Cột của bảng
  const columns = [
    {
      title: 'ID',
      key: 'BookId',
      dataIndex: 'bookId',
      render: (code, data) => {
        return (
          <a target="blank" href={`/product/${data.BookId}`}>
            {code}
          </a>
        )
      },
    },
    {
      title: 'Title',
      key: 'title',
      dataIndex: 'title',
      render: (name) => (
        <Tooltip title={name}>{helpers.reduceProductName(name, 40)}</Tooltip>
      ),
    },
    {
      title: 'Price',
      key: 'price',
      dataIndex: 'price',
      // defaultSortOrder: 'descend',
      sorter: (a, b) => a.price - b.price,
      render: (price) => (
        <h3 style={{ color: '#4F55C5' }}>
          {price ? helpers.formatProductPrice(price) : 'Liên hệ'}
        </h3>
      ),
    },
    {
      title: 'Category',
      key: 'category',
      dataIndex: 'category',
      filters: generateFilterType(),
      onFilter: (value, record) => record.category === value,
      render: (type) => helpers.convertProductType(type),
    },
    // {
    //   title: 'Thương hiệu',
    //   key: 'brand',
    //   dataIndex: 'brand',
    //   sorter: (a, b) => {
    //     if (a.brand < b.brand) return -1;
    //     if (a.brand > b.brand) return 1;
    //     return 0;
    //   },
    //   render: (brand) => (
    //     <Tooltip title={brand}>{helpers.reduceProductName(brand, 40)}</Tooltip>
    //   ),
    // },
    {
      title: 'In Stock',
      key: 'instock',
      dataIndex: 'instock',
      // defaultSortOrder: 'ascend',
      sorter: (a, b) => a.instock - b.instock,
    },
    {
      title: 'Discount',
      key: 'discount',
      dataIndex: 'discount',
      // defaultSortOrder: 'ascend',
      sorter: (a, b) => a.discount - b.discount,
      render: (discount) => `${discount} %`,
    },
    {
      title: 'Action',
      key: 'actions',
      fixed: 'right',
      width: 100,
      render: (book) => (
        <>
          <Tooltip title="Delete" placement="left">
            <DeleteOutlined
              onClick={() => setModalDel({ visible: true, id: book.BookId })}
              className="m-r-8 action-btn-product"
              style={{ color: 'red' }}
            />
          </Tooltip>
          <Tooltip title="Edit" placement="left">
            <EditOutlined
              onClick={() => {
                // console.log("SeeProduct 176: ")
                setEditModal({ visible: true, product: { ...book } });
              }}
              className="m-r-8 action-btn-product"
              style={{ color: '#444' }}
            />
          </Tooltip>

          <Tooltip title="See product" placement="left">
            <a target="blank" href={`/product/${book.bookId}`}>
              <EyeOutlined
                className="action-btn-product"
                style={{ color: '#444' }}
              />
            </a>
          </Tooltip>
        </>
      ),
    },
  ];

  // rendering ...
  return (
    <div className="pos-relative p-8 edit-modal">
      {/* {isLoading ? (
        <Spin
          tip="Loading ..."
          size="large"
          className="trans-center"
        />
      ) : ( */}
      {(
        <div className='m-lr-10 m-t-10'>
          {' '}
          {/* modal confirm delete product */}
          <Modal
            title="Xác nhận xoá sản phẩm"
            visible={modalDel.visible}
            onOk={() => {
              onDelete(modalDel._id);
              setModalDel({ visible: false, _id: false });
            }}
            onCancel={() => setModalDel({ visible: false, _id: false })}
            okButtonProps={{ danger: true }}
            okText="Xoá"
            cancelText="Huỷ bỏ">
            <WarningOutlined style={{ fontSize: 28, color: '#F7B217' }} />
            <b> Bạn chắc chắn muốn xóa sản phẩn này chứ ?</b>
          </Modal>
          {/* table show product list */}
          <AdminSearch
          options={[{id:0,text:"Tất cả"},{id:1,text:"Tên Sản phẩm"}]} 
          onSearch={()=>console.log("On Search")}
          />
          <Table
            pagination={{
              current: page,
              pageSize: 10,
              total: totalProduct,
              position: ['bottomCenter'],
              showSizeChanger: false,
              onChange: (p) => setPage(p)
            }}
            loading={isLoading}
            className="admin-see-product"
            columns={columns}
            dataSource={list}
          />
          {/* edit product modal */}
          <EditProductModal
            visible={editModal.visible}
            onClose={(value) => onCloseEditModal(value)}
            product={editModal.product}
          />
        </div>
      )}
    </div>
  );
}

export default SeeProduct;
